.flexBoard{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.flexChild{
  -ms-flex: 1 0 25%;
      flex: 1 0 25%;
  display: inline-block;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 5px;
  height: 100px;
  border-radius: 15px;
  border: solid 2px purple;
  background: -webkit-gradient(linear, left top, right top, from(#d660b1), color-stop(#fbad50),  to(#e95950));
  background: -webkit-linear-gradient(left, #d660b1, #fbad50,  #e95950);
  background: -o-linear-gradient(left, #d660b1, #fbad50,  #e95950);
  background: linear-gradient(to right, #d660b1, #fbad50,  #e95950);
}

.congrats{
    font-size: 18px;
    font-weight: bold;
    color: lightseagreen;
}

.cardFlipped{
    background: -webkit-gradient(linear, left top, right top, from(yellow),to(#fbad50));
    background: -webkit-linear-gradient(left, yellow,#fbad50);
    background: -o-linear-gradient(left, yellow,#fbad50);
    background: linear-gradient(to right, yellow,#fbad50);
}

.cardMatched{
    background: -webkit-gradient(linear, left top, right top, from(greenyellow),to(green));
    background: -webkit-linear-gradient(left, greenyellow,green);
    background: -o-linear-gradient(left, greenyellow,green);
    background: linear-gradient(to right, greenyellow,green);
}

.mainGame{
    margin-top: 30px;
    margin-bottom: 30px;
    -ms-flex-pack: center;
        justify-content: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}


